	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"5 Hours for a Lampshade","date":"24 Nov 2002","slug":"/2002-11-24-5-hours-for-a-lampshade/","tags":"Y2002"},"html":"<p>Mama returned from her excursion to Munich yesterday. Even though she has a more than functional company car, she wanted Christopher and I to pick her up from the airport. Why? Her car was at the office (taxi fare to the airport is cheaper than parking), but she wanted to swing by Ikea to pick up a replacement lampshade. Then we would drive her across town to her car. This didn't seem to me to be an efficient use of time, but I agreed anyway. Afterwards, I figured this little adventure cost me 5 hours of free time, which explains why I'm still tired today. But Christopher enjoyed the trip to the airport. </p>\n<p><b>Update</b> While cleaning the living room, Mama knocked the lamp over and broke the new lampshade, after barely 24 hours. Since the lamp had been shadeless for well over a year, I guess that means we'll get the next new lampshade in Spring 2004.</p>"},"next":{"fields":{"title":"Every Day in Every Way","date":"22 Nov 2002","slug":"/2002-11-22-every-day-in-every-way/"}},"previous":{"fields":{"title":"Muffin Man","date":"25 Nov 2002","slug":"/2002-11-25-muffin-man/"}}},{"node":{"fields":{"title":"Every Day in Every Way","date":"22 Nov 2002","slug":"/2002-11-22-every-day-in-every-way/","tags":"Y2002"},"html":"<p>Sorry, I've been paying more attention to my blogroll than to my blog today. I'll post details later, but just note that the blogroll now combines update data from both blo.gs and blogrollings, as well as HTTP headers for a few selected sites. So there are just two sites left for which I don't have update times. But they, too, will fall.</p>"},"next":{"fields":{"title":"Third Thursday in November","date":"21 Nov 2002","slug":"/2002-11-21-third-thursday-in-november/"}},"previous":{"fields":{"title":"5 Hours for a Lampshade","date":"24 Nov 2002","slug":"/2002-11-24-5-hours-for-a-lampshade/"}}},{"node":{"fields":{"title":"Third Thursday in November","date":"21 Nov 2002","slug":"/2002-11-21-third-thursday-in-november/","tags":"Y2002"},"html":"<p>Today is the third Thursday of November. That means it's <a href=\"http://wine.about.com/library/weekly/aa112102.htm\">Beaujolais Nouveau</a> day. Here it's called Beujolais Primeur. Whatever you call it, it's fresh French decadence for just &euro;1.49.</p>"},"next":{"fields":{"title":"Some Guys Have All The Luck","date":"21 Nov 2002","slug":"/2002-11-21-some-guys-have-all-the-luck/"}},"previous":{"fields":{"title":"Every Day in Every Way","date":"22 Nov 2002","slug":"/2002-11-22-every-day-in-every-way/"}}}],"pathPrefix":"","first":false,"last":false,"index":812,"pageCount":1101}}

